.button-forecast, .button-today {
  opacity: .54;
  color: #000;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #fff;
  border: 2px solid #fff;
  border-radius: 15px;
  outline: none;
  padding: 5px 38px;
  font-family: Lato, sans-serif;
  font-size: 14px;
  font-weight: 700;
}

.button-forecast:hover, .button-today:hover {
  opacity: 1;
}

html {
  box-sizing: border-box;
  font-family: Lato, sans-serif;
}

*, :after, :before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.hidden {
  display: none;
}

.selected {
  opacity: 1;
}

body {
  background: linear-gradient(0deg, rgba(0, 0, 0, .65) 0, rgba(0, 0, 0, .05)) 50% / cover no-repeat;
  padding: 30px 0 65px;
}

@media (min-width: 768px) {
  body {
    padding: 67px 0 74px;
  }
}

@media screen and (min-width: 1120px) {
  body {
    padding-bottom: 0;
  }
}

.container {
  width: 280px;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .container {
    width: 608px;
  }
}

@media screen and (min-width: 1120px) {
  .container {
    width: 1060px;
  }
}

.forecast, .forecast-wrap {
  text-align: center;
}

.forecast {
  color: #fff;
  background-color: rgba(16, 33, 54, .933);
  border-radius: 35px;
  padding: 34px 15px 26px;
  font-family: Lato, sans-serif;
  box-shadow: 0 8px 43px rgba(16, 33, 54, .8);
}

.forecast__days {
  width: 226px;
  white-space: nowrap;
  justify-content: flex-start;
  margin: 0 auto 18px;
  padding-bottom: 18px;
  list-style: none;
  display: flex;
  overflow-x: scroll;
  overflow-y: hidden;
}

.forecast__days::-webkit-scrollbar {
  -webkit-appearance: none;
  height: 2px;
}

.forecast__days::-webkit-scrollbar-track {
  background-color: rgba(255, 255, 255, .2);
}

.forecast__days::-webkit-scrollbar-thumb {
  background-color: #ff6b08;
  border-radius: 2px;
}

.forecast__days-item {
  width: 75px;
  cursor: pointer;
  display: inline-block;
}

.forecast__days-item:not(:last-child) {
  margin-right: 25px;
}

.forecast__days-item * {
  pointer-events: none;
}

.forecast__city-name {
  margin-bottom: 22px;
  font-size: 18px;
  font-weight: 700;
}

.forecast__day-name {
  opacity: .54;
  margin-bottom: 18px;
  font-size: 12px;
  font-weight: 400;
}

.forecast__day-name--active {
  opacity: 1;
  color: #ff6b08;
}

.forecast__date {
  margin-bottom: 6px;
  font-size: 16px;
  font-weight: 400;
}

.forecast__icon {
  width: 50px;
  height: 50px;
  margin: 0 auto 8px;
}

.forecast__extremum-wrap {
  justify-content: space-between;
  display: flex;
}

.forecast__extremum:first-of-type {
  border-right: 1px solid rgba(255, 255, 255, .2);
  padding-right: 5px;
}

.forecast__extremum:last-of-type {
  padding-left: 4px;
}

.forecast__extremum-title {
  opacity: .54;
  font-size: 12px;
  font-weight: 400;
  line-height: 22px;
}

.forecast__extremum-value {
  color: #ff6b08;
  font-size: 16px;
  font-weight: 700;
}

.forecast__extremum-value:after {
  content: "째";
  padding-left: 2px;
}

.forecast__arrow-warp {
  width: 58px;
  justify-content: space-between;
  margin: 0 auto;
  display: flex;
}

.forecast__btn {
  color: #fff;
  background-color: initial;
  cursor: pointer;
  border: none;
  padding: 10px;
  position: relative;
}

.forecast__btn--left:before, .forecast__btn--right:before {
  content: "";
  width: 6px;
  height: 6px;
  border-top: 1px solid #fff;
  border-left: 1px solid #fff;
  display: inline-block;
  position: absolute;
  top: 50%;
}

.forecast__btn--left:before {
  right: 45%;
  transform: translate(50%, -50%)rotate(-45deg);
}

.forecast__btn--right:before {
  left: 45%;
  transform: translate(-50%, -50%)rotate(135deg);
}

@media (min-width: 768px) {
  .city-name {
    margin-bottom: 28px;
    display: block;
  }

  .forecast__days {
    width: 100%;
    white-space: nowrap;
    justify-content: space-between;
    margin-bottom: 12px;
    padding: 0 50px;
    overflow-x: hidden;
  }

  .forecast__days-item {
    width: auto;
  }

  .forecast__city-name {
    font-size: 28px;
  }

  .forecast__day-name {
    font-size: 14px;
  }

  .forecast__date {
    font-size: 18px;
  }

  .forecast__extremum:first-of-type {
    padding-right: 11px;
  }

  .forecast__extremum-title {
    font-size: 14px;
  }

  .forecast__extremum-value {
    font-size: 18px;
  }

  .forecast__arrow-warp {
    display: none;
  }
}

@media screen and (min-width: 1120px) {
  .forecast-wrap {
    margin-bottom: 74px;
  }

  .forecast__days {
    padding: 0 116px;
  }

  .forecast__day-name {
    font-size: 18px;
  }

  .forecast__date {
    font-size: 25px;
  }

  .forecast__extremum:first-of-type {
    padding-right: 6px;
  }

  .forecast__extremum-title {
    font-size: 18px;
  }

  .forecast__extremum-value {
    font-size: 25px;
  }
}

.forecast-info {
  max-height: 0;
  transition: all .2s linear;
  overflow: hidden;
}

.forecast-info__list {
  white-space: nowrap;
  padding-top: 18px;
  padding-bottom: 20px;
  list-style: none;
  overflow-x: scroll;
  overflow-y: hidden;
}

.forecast-info__list::-webkit-scrollbar {
  -webkit-appearance: none;
  height: 2px;
}

.forecast-info__list::-webkit-scrollbar-track {
  background-color: rgba(255, 255, 255, .2);
}

.forecast-info__list::-webkit-scrollbar-thumb {
  background-color: #ff6b08;
  border-radius: 2px;
}

.forecast-info__list-item {
  width: 120px;
  border: 1px solid rgba(255, 255, 255, .2);
  border-radius: 25px;
  padding: 14px 6px;
  display: inline-block;
}

.forecast-info__list-item:not(:last-child) {
  margin-right: 7px;
}

.forecast-info__list-item:hover {
  background-color: rgba(255, 255, 255, .2);
}

.forecast-info__time {
  opacity: .54;
  margin-bottom: 12px;
  font-size: 12px;
  font-weight: 400;
}

.forecast-info__icon {
  width: 50px;
  height: 50px;
  margin: 0 auto 8px;
}

.forecast-info__temp {
  color: #ff6b08;
  margin-bottom: 18px;
  font-size: 18px;
  font-weight: 700;
}

.forecast-info__temp:after {
  content: "째";
  padding-left: 2px;
}

.forecast-info__details-list {
  list-style: none;
}

.forecast-info__details-item {
  padding-left: 26px;
  position: relative;
}

.forecast-info__details-item:before {
  content: "";
  display: inline-block;
  position: absolute;
  left: 0;
}

.forecast-info__details-item--precipitation:before {
  width: 22px;
  height: 20px;
  background: url("data:image/png;base64,");
}

.forecast-info__details-item--humidity:before {
  width: 13px;
  height: 20px;
  background: url("data:image/png;base64,");
}

.forecast-info__details-item--wind:before {
  width: 24px;
  height: 20px;
  background: url("data:image/png;base64,");
}

.forecast-info__details-item:not(:last-of-type) {
  margin-bottom: 10px;
}

@media (min-width: 768px) {
  .forecast-info {
    position: relative;
  }

  .forecast-info__time {
    font-size: 14px;
  }
}

@media screen and (min-width: 1120px) {
  .forecast-info__list {
    white-space: nowrap;
    overflow-x: hidden;
  }

  .forecast-info__list-item {
    width: 118px;
  }

  .forecast-info__temp {
    font-size: 25px;
  }

  .forecast-info__btn {
    display: none;
  }
}

