.toolbar {
  justify-content: flex-end;
  padding: 16px 24px;
  display: flex;
}

.theme-switch {
  transition: color .15s ease-in 0;
  color: rgba(16, 33, 54, .6);
  align-items: center;
  font-size: 20px;
  font-weight: 700;
  display: flex;
}

.theme-switch__icon {
  color: currentColor;
  height: 26px;
  margin-left: 8px;
  margin-right: 8px;
}

.switch {
  width: 64px;
  height: 32px;
  border-radius: 50em;
  flex-shrink: 0;
  align-items: center;
  margin: 0 10px;
  padding: 3px 0;
  display: flex;
  position: relative;
}

.switch__input, .switch__label {
  position: absolute;
  top: 0;
  left: 0;
}

.switch__input {
  opacity: 0;
  height: 0;
  width: 0;
  pointer-events: none;
  margin: 0;
  padding: 0;
}

.switch__input:checked + .switch__label + .switch__marker {
  left: calc(100% - 29px);
}

.switch__label {
  width: 100%;
  height: 100%;
  color: rgba(0, 0, 0, 0);
  -webkit-user-select: none;
