.button-forecast,.button-today {
    padding: 5px 38px;
    border-radius: 15px;
    border: 2px solid #fff;
    background-color: #fff;
    opacity: .54;
    color: #000;
    font-family: Lato,sans-serif;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    cursor: pointer;
    outline: none
}

.button-forecast:hover,.button-today:hover {
    opacity: 1
}

html {
    box-sizing: border-box;
    font-family: Lato,sans-serif
}

*,:after,:before {
    box-sizing: inherit;
    margin: 0;
    padding: 0
}

img {
    max-width: 100%;
    height: auto;
    display: block
}

.hidden {
    display: none
}

.selected {
    opacity: 1
}

body {
    background-image: linear-gradient(0deg,rgba(0,0,0,.65) 0,rgba(0,0,0,.05));
    background: linear-gradient(0deg,rgba(0,0,0,.65) 0,rgba(0,0,0,.05));
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
