.forecast,.forecast-wrap {
    text-align: center
}

.forecast {
    padding: 34px 15px 26px;
    font-family: Lato,sans-serif;
    color: #fff;
    box-shadow: 0 8px 43px rgba(16,33,54,.8);
    border-radius: 35px;
    background-color: rgba(16,33,54,.933333)
}

.forecast__days {
    padding-bottom: 18px;
    width: 226px;
    display: flex;
    justify-content: flex-start;
    list-style: none;
    margin: 0 auto 18px;
    overflow: hidden;
    overflow-x: scroll;
    white-space: nowrap
}

.forecast__days::-webkit-scrollbar {
    -webkit-appearance: none;
    height: 2px
}

.forecast__days::-webkit-scrollbar-track {
    background-color: hsla(0,0%,100%,.2)
}

.forecast__days::-webkit-scrollbar-thumb {
    border-radius: 2px;
    background-color: #ff6b08
}

.forecast__days-item {
    width: 75px;
    cursor: pointer;
    display: inline-block
}

.forecast__days-item:not(:last-child) {
    margin-right: 25px
}

.forecast__days-item * {
    pointer-events: none
}

.forecast__city-name {
    margin-bottom: 22px;
    font-size: 18px;
    font-weight: 700
}

.forecast__day-name {
    margin-bottom: 18px;
    opacity: .54;
    font-size: 12px;
    font-weight: 400
}

.forecast__day-name--active {
    opacity: 1;
    color: #ff6b08
}

.forecast__date {
    margin-bottom: 6px;
    font-size: 16px;
    font-weight: 400
}

.forecast__icon {
    width: 50px;
    height: 50px;
    margin: 0 auto 8px
}

.forecast__extremum-wrap {
    display: flex;
    justify-content: space-between
}

.forecast__extremum:first-of-type {
    padding-right: 5px;
    border-right: 1px solid hsla(0,0%,100%,.2)
}

.forecast__extremum:last-of-type {
    padding-left: 4px
}

.forecast__extremum-title {
    opacity: .54;
    font-size: 12px;
    font-weight: 400;
    line-height: 22px
}

.forecast__extremum-value {
    color: #ff6b08;
    font-size: 16px;
    font-weight: 700
}

.forecast__extremum-value:after {
    content: "\00B0";
    padding-left: 2px
}

.forecast__arrow-warp {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    width: 58px
}

.forecast__btn {
    padding: 10px;
    position: relative;
    color: #fff;
    background-color: initial;
    border: none;
    cursor: pointer
}

.forecast__btn--left:before,.forecast__btn--right:before {
    content: "";
    display: inline-block;
    width: 6px;
    height: 6px;
    border-left: 1px solid #fff;
    border-top: 1px solid #fff;
    position: absolute;
    top: 50%
}

.forecast__btn--left:before {
    right: 45%;
    transform: translate(50%,-50%) rotate(-45deg)
}

.forecast__btn--right:before {
    left: 45%;
    transform: translate(-50%,-50%) rotate(135deg)
}

@media (min-width: 768px) {
    .city-name {
        display:block;
        margin-bottom: 28px
    }

    .forecast__days {
        padding: 0 50px;
        margin-bottom: 12px;
        width: 100%;
        justify-content: space-between;
        white-space: nowrap;
        overflow-x: hidden
    }

    .forecast__days-item {
        width: auto
    }

    .forecast__city-name {
        font-size: 28px
    }

    .forecast__day-name {
        font-size: 14px
    }

    .forecast__date {
        font-size: 18px
    }

    .forecast__extremum:first-of-type {
        padding-right: 11px
    }

    .forecast__extremum-title {
        font-size: 14px
    }

    .forecast__extremum-value {
        font-size: 18px
    }

    .forecast__arrow-warp {
        display: none
    }
}

@media screen and (min-width: 1120px) {
