.toolbar {
    display: flex;
    justify-content: flex-end;
    padding: 16px 24px
}

.theme-switch {
    display: flex;
    align-items: center;
    transition: color .15s ease-in 0;
    font-size: 20px;
    font-weight: 700;
    color: rgba(16,33,54,.6)
}

.theme-switch__icon {
    color: currentColor;
    height: 26px;
    margin-right: 8px;
    margin-left: 8px
}

.switch {
    display: flex;
    flex-shrink: 0;
    align-items: center;
    position: relative;
    width: 64px;
    height: 32px;
    border-radius: 50em;
    padding: 3px 0;
    margin: 0 10px
}

.switch__input,.switch__label {
    position: absolute;
    left: 0;
    top: 0
}

.switch__input {
    margin: 0;
    padding: 0;
    opacity: 0;
    height: 0;
    width: 0;
    pointer-events: none
}

.switch__input:checked+.switch__label+.switch__marker {
    left: calc(100% - 29px)
}

.switch__label {
    width: 100%;
    height: 100%;
    color: transparent;
    -webkit-user-select: none;
    user-select: none;
    background-color: #ff6b08;
    border-radius: inherit;
    z-index: 1;
    transition: background-color .15s;
    cursor: pointer
}

.switch__marker {
    position: relative;
    background-color: #fff;
    width: 26px;
    height: 26px;
    border-radius: 50%;
    z-index: 2;
    pointer-events: none;
    box-shadow: 0 1px 1px rgba(0,0,0,.25);
    left: 3px;
    transition: left .2s;
    will-change: left
}

.celsius,.fahrenheit {
    padding: 1px 3px;
    border: 2px solid rgba(0,0,0,.25);
    border-radius: 5px;
    background-color: hsla(0,0%,100%,.25)
}

.isActiveColor {
    color: #ff6b08;
    border-color: #ff6b08;
    transition: all .3s linear;
    background-color: initial
}

@media (min-width: 768px) {
    .toggle {
        width:490px
    }
}

@media screen and (min-width: 1120px) {
    .toggle {
        margin-right:0
    }
}
